
.tb {
	&--a {
		border-radius: 15%;
		width: 40px;
		height: 40px;
		overflow: hidden; } }
.footer--sites {
	.tb {
		&--a {
			border-radius: 50%; } } }
.smallSiteList--thumb {
	width: 40px;
	height: 40px;
	display: block;
	background-position: var(--p, 100% 100%);
	background-color: var(--b, red);
	background-image: url("../img/thumb_.jpg");
	filter: contrast(2); }


@each $site in $thumb-list {
	$branding-primary: nth($site, 4);
	$x: nth($site, 2);
	$y: nth($site, 3);

	.#{nth($site, 1)} {
		//background: linear-gradient(135deg , #280047 0% , black 50% , $branding-primary 100%)
		--b: $branding-primary;
		--p: $x $y; } }

.siteOffer--head {
	/* can be treated like a fallback */
	/* will be "on top", if browser supports it */
	background-image: linear-gradient(135deg , darken(#5C0C9B, 15%) 10% , #001c4f 20%, #000a1b 35% , rgba(white, 0) 75%);
	background-position: var(--p, 0 0); }
